.App {
  text-align: center;
}

.Name-header {
  font-weight: bold;
  padding: 4px;
  color: #121212;
  background-color: rgb(233, 233, 233);
}

.Description-container {
  font-size: 14pt;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}

.Description {
  padding: 4px;
  color: rgb(233,233,233);
  transition: 0.75s;
}


.Description:hover {
  background: linear-gradient(90deg, #b6b6b6 0%,#dadada 10%,#fed6dc 20%, #eef0ff 40%, #fec7dd 50%,#d8d8d8 80%);
  background-position: left center;
  color: #121212;
}

.App-header {
  background-color: #121212;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Connections-container {
  padding: 12px;
}

.Link {
  font-size: 16pt;
  color: #d3d3d3;
}